@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');

:root {
  --bgimg: url("../public//back10.jpg");
  --white: #ffffff; 
}
html {
  min-height: 100%;
  margin:0; padding:0; 
  background-image: var(--bgimg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-color:#2E3192;
  &::-webkit-scrollbar {
    width: 8px;
}

&::-webkit-scrollbar-track {
    background-color: #eceef2;
    border-radius: 0px;
}

&::-webkit-scrollbar-thumb {
    background-color: #1D0002;
    border-radius: 0px;
    
}
}
body {
  overflow-x: hidden;

  margin:0; padding:0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#score {font-family: 'Poppins', sans-serif;}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 1000px white inset !important;
}

textarea {


  resize: none; /*remove the resize handle on the bottom right*/
}

#inputadmin::placeholder {
  position: absolute;
  left: 15px;
}

#msg{-webkit-text-stroke: 0.1px black;letter-spacing: "0px";}

#time{-webkit-text-stroke: 0.5px black;letter-spacing: "0px";}

#captxtarea{width: 650px;}
#cap{font-family: impact;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow:2px 2px 0 #000,
  -2px -2px 0 #000,
  2px -2px 0 #000,
  -2px 2px 0 #000,
  0px 2px 0 #000,
  2px 0px 0 #000,
  0px -2px 0 #000,
  -2px 0px 0 #000,
  2px 2px 5px #000;}



#caprate{font-family: impact;
  text-transform: uppercase;
  font-size: 30px;
  color: white;
  text-align: center;
  letter-spacing: 3px;
  text-shadow:2px 2px 0 #000,
  -2px -2px 0 #000,
  2px -2px 0 #000,
  -2px 2px 0 #000,
  0px 2px 0 #000,
  2px 0px 0 #000,
  0px -2px 0 #000,
  -2px 0px 0 #000,
  2px 2px 5px #000;line-height: 45px;}

.MuiTextarea-textarea{
  font-family: impact;
  text-transform: uppercase;
  font-size: 30px;
  color: white;
  text-align: center;
  letter-spacing: 3px;
  text-shadow:2px 2px 0 #000,
  -2px -2px 0 #000,
  2px -2px 0 #000,
  -2px 2px 0 #000,
  0px 2px 0 #000,
  2px 0px 0 #000,
  0px -2px 0 #000,
  -2px 0px 0 #000,
  2px 2px 5px #000;}

#msg{-webkit-text-stroke: 0.1px black;letter-spacing: "0px";}
.stfc{font-size: 30px;}

#cap::placeholder {
  position: absolute;
  left: 15px;
  color:rgb(228, 228, 228);
  -webkit-text-stroke: 1px rgb(130, 130, 130);
}

#messages{font-family: impact;
  text-transform: uppercase;
  color: white;
  letter-spacing: 1px;
  text-shadow:2px 2px 0 #000,
  -2px -2px 0 #000,
  2px -2px 0 #000,
  -2px 2px 0 #000,
  0px 2px 0 #000,
  2px 0px 0 #000,
  0px -2px 0 #000,
  -2px 0px 0 #000,
  2px 2px 5px #000;}

#homebox{margin-top: 160px;}

#itembox{width: 350px;box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;}
#adminbox{position: relative;top: calc(50vh - 150px);}
#friendsbox{flex-direction: column;position: fixed;top: 100px;
  &::-webkit-scrollbar {
    width: 5px;
}

&::-webkit-scrollbar-track {
    background-color: #eceef2;
    border-radius: 0px;
    margin-top: 20px;
    margin-bottom: 20px;
}

&::-webkit-scrollbar-thumb {
    background-color: #9b9b9b;
    border-radius: 0px;
    
}
}


#frienditem{margin-top: 8px;}

#gifbox{width: 480px;height: 270px;}

#ghostbox{position: relative;margin-top: 10px;}

@media screen and (max-width:912px) and (orientation: portrait) {
  #adminbox{top: calc(50vh - 230px);}
  #homebox{margin-top: calc(50vh - 150px);}
}

@media screen and (max-width:700px) {
  #ghostbox{position: relative;margin-top: 100px;}
  #frienditem{margin: 0px;}
  #captxtarea{width: 96vw;}
  .MuiTextarea-textarea{height: 30px;}

  #friendsbox{flex-direction: row;position: fixed;top:calc(100vh - 86px);width: max-content;}

  #gifbox{width: 100vw;height: 270px;}
  .rate{position: relative;top:10px;}
  #cap{width: 95vw;}

}


@media screen and (max-width:400px) {
.stfc{font-size: 20px;}
}